'use client';
import { useState } from 'react';

export default function Textos({ textos }: { textos: Array<{ titulo: string; contenido: string }> }) {
  const [abierto, setAbierto] = useState<number | null>(null);

  return (
    <div className="max-w-3xl mx-auto space-y-4">
      {textos.map((texto, index) => (
        <div key={index} className="border rounded-lg overflow-hidden">
          <button
            onClick={() => setAbierto(abierto === index ? null : index)}
            className="w-full p-4 text-left bg-gray-50 hover:bg-gray-100 flex justify-between items-center"
          >
            <span className="font-medium">{texto.titulo}</span>
            <span>{abierto === index ? 'âˆ’' : '+'}</span>
          </button>
          {abierto === index && (
            <div className="p-4 bg-white">
              <p className="leading-relaxed">{texto.contenido}</p>
            </div>
          )}
        </div>
      ))}
    </div>
  );
}